@font-face {
  font-family: "Digital";
  font-style: normal;
  font-weight: 400;
  src: url("/work-tracker/static/media/digital-7 (mono).58045dab.ttf");
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  background-image: url(https://pp.userapi.com/c845523/v845523713/cd5dc/01aQ6hG3Tlo.jpg);
  background-size: 1024px;
}
@media (min-width: 1024px), (min-height: 1024px) {
  body {
    background-size: 100vmax;
  }
}

#root {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.icon {
  margin: 0 5px;
  font-size: 15px;
}
.pad {
  padding: 10px;
  font-size: 23px;
  color: #bbb;
  text-align: center;
  cursor: pointer;
  margin-top: 15px;
  border-radius: 5px;
  border: 1px #333 solid;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.pad-start-pause {
  background: -webkit-linear-gradient(310deg, #3d983d 0%, #44aa44 50%, #2e732e 53%, #204f20 65%);
  background: linear-gradient(140deg, #3d983d 0%, #44aa44 50%, #2e732e 53%, #204f20 65%);
  }

.pad-start-pause:active {
  background: -webkit-linear-gradient(310deg, #2d862d 0%, #339933 50%, #206020 53%, #133913 65%);
  background: linear-gradient(140deg, #2d862d 0%, #339933 50%, #206020 53%, #133913 65%);
}

.pad-start-pause.pressed {
  background: -webkit-linear-gradient(310deg, #2d862d 0%, #339933 50%, #206020 53%, #133913 65%);
  background: linear-gradient(140deg, #2d862d 0%, #339933 50%, #206020 53%, #133913 65%);
}

.pad-reset {
  background: -webkit-linear-gradient(310deg, #951e1e 0%, #aa2222 50%, #6a1515 53%, #400d0d 65%);
  background: linear-gradient(140deg, #951e1e 0%, #aa2222 50%, #6a1515 53%, #400d0d 65%);
}

.pad-reset:active {
  background: -webkit-linear-gradient(310deg, #820e0e 0%, #991111 50%, #540909 53%, #260404 65%);
  background: linear-gradient(140deg, #820e0e 0%, #991111 50%, #540909 53%, #260404 65%);
}
.display-wrapper {
    background: #ddd;
    border-radius: 5px;
    height: auto;
    padding: 7px 12px;
    color: #111;
    font-family: Digital;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    cursor: default;
}

.display-total, .display-current {
    position: relative;
    z-index: 1;
}

.display-current {
    font-size: 40px;
}

.indicators-wrapper {
    display: -webkit-flex;
    display: flex;
}

.indicators-total-wrapper {
    font-size: 27px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.inactive {
    color: #bbb;
}

.time:hover {
    -webkit-animation: flash  0.5s infinite ease-in-out;
            animation: flash  0.5s infinite ease-in-out;
}

@-webkit-keyframes flash {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

@keyframes flash {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

.digit {
    position: relative;
    z-index: 1;
}

.digit::before {
    content: '8';
    color: #bbb;
    position: absolute;
    z-index: -1;
}
.device {
  border-radius: 5px;
  border: 1px #333 solid;
  background: -webkit-linear-gradient(310deg, #262626 0%, #333333 50%, #0d0d0d 53%, black 65%);
  background: linear-gradient(140deg, #262626 0%, #333333 50%, #0d0d0d 53%, black 65%);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 15px;
}

.label {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.sign-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: 18px;
  margin: 8px;
}

.sign-link {
  color: #000;
  text-decoration: none;
}

.sign-link:hover {
  color: #11a;
  text-decoration: underline;
}
